<template>
  <div class="wrapper">
    <div class="table-setter">
      <Input
          v-model="searchContent"
          placeholder="æœç´¢é¢˜ç›® åç§°ã€å†…å®¹æˆ–ç¼–å·"
          class="input"
          search
          @on-search="search"/>
      <router-link :to="{}">
        <Icon type="md-document" class="solving-icon"/>
        <div style="display: inline-block" @click="flushTableData">é‡è®¾</div>
      </router-link>
      <Dropdown
          @on-click="ChangeFilter($event, 'difficulty')"
          :style="{ cursor: 'pointer' }">
        éš¾åº¦
        <Icon type="ios-arrow-down"/>
        <DropdownMenu slot="list">
          <div @click="changeDiff(1)">
            <DropdownItem style="color: green" name="easy">ç®€å•</DropdownItem>
          </div>
          <div @click="changeDiff(2)">
            <DropdownItem style="color: orange" name="medium">ä¸­ç­‰</DropdownItem>
          </div>
          <div @click="changeDiff(3)">
            <DropdownItem style="color: red" name="hard">å›°éš¾</DropdownItem>
          </div>
        </DropdownMenu>
      </Dropdown>
      <Dropdown
          @on-click="ChangeFilter($event, 'solved')"
          :style="{ cursor: 'pointer' }">çŠ¶æ€
        <Icon type="ios-arrow-down"/>
        <DropdownMenu slot="list">
          <div @click="changeSolved('false')">
            <DropdownItem name="unsolved">æœªè§£ç­”</DropdownItem>
          </div>
          <div @click="changeSolved('true')">
            <DropdownItem name="solved">å·²è§£ç­”</DropdownItem>
          </div>
        </DropdownMenu>
      </Dropdown>
      <Dropdown
          @on-click="ChangeFilter($event, 'tag')" :style="{ cursor: 'pointer' }">æ ‡ç­¾
        <Icon type="ios-arrow-down"/>
        <DropdownMenu slot="list">
          <DropdownItem v-for="item in problemsTagList" :key="item" :name="item">
            {{ item.title }}
          </DropdownItem>
        </DropdownMenu>
      </Dropdown>
    </div>
    <Table :columns="columnNames"
           :data="problemsSet">
      <template v-slot:finish="{row}">
        <i v-if="row.solved==='1'" class="el-icon-check"></i>
      </template>
      <template v-slot:id="{row}">
        {{row.id}}
      </template>
      <template v-slot:title="{row}">
        {{row.title}}
      </template>
      <template v-slot:difficulty="{row}">
        <span v-if="row.difficulty===1" style="color: green">ç®€å•</span>
        <span v-else-if="row.difficulty===2" style="color: orange">ä¸­ç­‰</span>
        <span v-else-if="row.difficulty===3" style="color: red">å›°éš¾</span>
      </template>
      <template v-slot:passRate="{row}">
        {{renderRate(row.passRate)}}
      </template>
      <template v-slot:nb_submit="{row}">
        {{row.nb_submit}}
      </template>
      <template v-slot:op="{row}">
        <el-button @click="goToProblemDetail(row.id)" type="text" size="small">å¼€å§‹åšé¢˜</el-button>
      </template>
    </Table>
    <br>
    <el-button class="page-ctl-btn" @click="prePage" icon="el-icon-arrow-left" circle></el-button>
    <div style="display: inline-block;" v-for="index in range(pageCount)" :key="index">
      <el-button class="selected-page" v-if="index+1===curPage">
        {{index+1}}
      </el-button>
      <el-button class="un-selected-page" v-else type="text" @click="page=index+1" style="padding-left: 5px;padding-right: 5px">
        {{index+1}}
      </el-button>
    </div>
    <el-button class="page-ctl-btn" @click="nextPage" icon="el-icon-arrow-right" circle></el-button>
  </div>
</template>

<script>
export default {
  name: "TheProblemTable",
  data: function () {
    return {
      curPage_:1,
      pageCount:1,
      pageSize:10,
      columnNames: [
        {title: "å®Œæˆ", slot: "finish"}, {title: "ç¼–å·", slot: "id"}, {title: "æ ‡é¢˜", slot: "title"},
        {title: "éš¾åº¦", slot: "difficulty"}, {title: "æ­£ç¡®ç‡", slot: "passRate"}, {title: "æäº¤æ•°", slot: "nb_submit"},
        {title: "æ“ä½œ", slot: "op"}],
      searchContent: "",
      problemsSet: [],
      problemsTagList:[]
    };
  },
  created: async function () {
    await this.flushTableData()
    await this.$axios.get("/bank/tags")
        .then(rsp=>{
          this.problemsTagList=rsp.data.tagList
        })
  },
  computed: {
    curPage:{
      get:function () {
        return this.curPage_
      },
      set:async function (value){
        this.curPage_=value
        await this.flushTableData()
      }
    },
    filtedProblemsTableData: function () {
      if (!Array.isArray(this.problems)) {
        return [];
      }
      return this.problems.filter((data) => {
        const query = this.$route.query;
        const PassRecord = this.userMessage?.PassRecord?.[data.index];
        const type = this.$route.params.type;
        if (type != "all" && type != "algorithms") {//ä¸Šé¢çš„æŒ‰é’®
          return false;
        }
        if (query.search && !data.name.includes(query.search)) {//æœç´¢æ¡†
          return false;
        }
        if (query.difficulty && query.difficulty != data.difficulty) {//éš¾åº¦
          return false;
        }
        if (//çŠ¶æ€
            query.solved &&
            query.solved != PassRecord &&
            (query.solved != "unsolved" || PassRecord != null)
        ) {
          return false;
        }
        if (query.tag && !data.tags.includes(query.tag)) {
          return false
        }
        return true;
      });
    },
  },
  methods: {
    search:async function(){
      await this.$axios.get("/bank/questions", {params: {title:this.searchContent,curPage: this.curPage, pageSize: this.pageSize}})
          .then((rsp) => {
            if (rsp.status === 200) {
              this.problemsSet = rsp.data.questionList
              this.pageCount=rsp.data.pageCount
            }
          })
          .catch((data) => {
            console.log(data)
          })
    },
    changeDiff:async function (diff) {
      await this.$axios.get("/bank/questions", {params: {difficulty:diff,curPage: this.curPage, pageSize: this.pageSize}})
          .then((rsp) => {
            if (rsp.status === 200) {
              this.problemsSet = rsp.data.questionList
              this.pageCount=rsp.data.pageCount
            }
          })
          .catch((data) => {
            console.log(data)
          })
    },
    changeSolved:async function(status){
      await this.$axios.get("/bank/questions", {params: {solved:status,curPage: this.curPage, pageSize: this.pageSize}})
          .then((rsp) => {
            if (rsp.status === 200) {
              this.problemsSet = rsp.data.questionList
              this.pageCount=rsp.data.pageCount
            }
          })
          .catch((data) => {
            console.log(data)
          })
    },
    flushTableData:async function(){
      await this.$axios.get("/bank/questions", {params: {curPage: this.curPage, pageSize: this.pageSize}})
          .then((rsp) => {
            if (rsp.status === 200) {
              this.problemsSet = rsp.data.questionList
              this.pageCount=rsp.data.pageCount
            }
          })
          .catch((data) => {
            console.log(data)
          })
    },
    prePage(){
      if(this.curPage<=1){
        return
      }
      this.curPage--
    },
    nextPage(){
      if(this.curPage>=this.pageCount){
        return
      }
      this.curPage++
    },
    range(i){
      return Array.from(Array(i).keys());
    },
    renderRate(rate){
      return Math.floor(rate)+"."+Math.floor(rate*100%100)+"%"
    },
    // eslint-disable-next-line no-unused-vars
    goToProblemDetail: function (qid) {
      this.$router.push({name: 'Problem', params: {id: qid}})
    },
    ChangeFilter: function (value, key) {
      let query = JSON.parse(JSON.stringify(this.$route.query));
      if (value == null || value.length === 0) {
        delete query[key];
      } else {
        query[key] = value;
      }
      this.$router.push({query});
    },
  },
};
</script>

<style lang="scss" scoped>
.tag-list {
  height: 500px;
  overflow-y: scroll;
}

