<script>
export default {
  data:function(){
    return{
      finishList:[]
    }
  },
  name: "TheProgress",
  props: ["difficultyCount", "solvedCount"],
  methods: {
    //å®Œæˆç‡
    rate: function () {
      console.log(this.finishList)
      let finish=0
      let total=0
      for(let i=1;i<=3;i++){
        finish+=this.finishList.finish[i]
        total+=this.finishList.total[i]
      }
      return (finish / total).toFixed(4) * 100
    },
  },
  created:async function () {
    await this.$axios.get("bank/question/finish")
        .then(rsp=>{
          this.finishList=rsp.data
        })
  }
};
